import { useState, useMemo, useEffect, useCallback } from "react";
import useCloudinary from '../../hooks/useCloudinary';
import {
  SectionContainer,
  Button,
  IconWrap,
  ButtonIcon,
} from "../StyledComponents";
import {
  NewWrapper,
  TopLine,
  Heading,
  TextWrapper,
  Subtitle,
  CenterVideo,
  VideoContainer,
  StyledReactPlayer,
  BtnWrap,
  ResponsiveSectionBg
} from "./NewElements.js";

export const NewSection = () => {
  const [hover, setHover] = useState(false);
  const [imageUrl, setImageUrl] = useState("");

  const cloudinaryInstance = useCloudinary();

  const desktopUrl = useMemo(() => {
    return cloudinaryInstance.image("NEWD_xzlbos").toURL({
      transformation: [
        { width: 1920, crop: "scale" },
        { quality: "auto:good" },
        { fetch_format: "auto" },
      ],
    });
  }, [cloudinaryInstance]);

  const mobileUrl = useMemo(() => {
    return cloudinaryInstance.image("case3_hzwjek").toURL({
      transformation: [
        { width: 480, height: 800, crop: 'fill' },
        { quality: 'auto' },
        { fetch_format: 'auto' }
      ]
    });
  }, [cloudinaryInstance]);

  const handleResize = useCallback(() => {
    if (window.innerWidth <= 480) {
      setImageUrl(mobileUrl);
    } else {
      setImageUrl(desktopUrl);
    }
  }, [desktopUrl, mobileUrl]);

  useEffect(() => {
    handleResize(); // Set initial image URL based on current window size
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, [handleResize]);

  const onHover = () => {
    setHover(!hover);
  };

  return (
    <SectionContainer id="new">
      <ResponsiveSectionBg desktopUrl={desktopUrl} mobileUrl={mobileUrl} />
      <NewWrapper style={{ zIndex: "20" }}>
        <TopLine>New Release</TopLine>
        <Heading>Higher Power</Heading>
        <CenterVideo>
          <VideoContainer>
            <StyledReactPlayer
              loading="lazy"
              width="100%"
              height="100%"
              controls={true}
              url="https://youtu.be/GriWw3Sk6Bk"
            />
          </VideoContainer>
        </CenterVideo>
        <TextWrapper>
          <Subtitle>
            The official Music video, "Message to God", is now available on
            YouTube.
          </Subtitle>
        </TextWrapper>
        <BtnWrap>
          <Button
            to="thoughts"
            smooth={true}
            duration={500}
            spy={true}
            exact="true"
            offset={-80}
            onMouseEnter={onHover}
            onMouseLeave={onHover}
          >
            Continue
            <IconWrap>
              <ButtonIcon />
            </IconWrap>
          </Button>
        </BtnWrap>
      </NewWrapper>
    </SectionContainer>
  );
};

export default NewSection;
